body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(37, 38, 58);
  max-width: 1400px;
  width: 100%;
  height: 101vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
}
h2,
h3,
p {
  color: white;
}

/* PROFILE  */
.Business__card {
  max-width: 320px;
  width: 100%;
  border-radius: 10px;
  height: auto;
  background-color: rgb(20, 20, 30);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}
.profilePic {
  width: 320px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

/* INFO */

.Info__wrapper {
  width: 100%;
}
.Info__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100px;
  line-height: 0.1px;
  padding: 10px 0px;
}
.Info__name {
  font-size: 23px;
}
.Info__para-1,
.Info__para-2 {
  font-size: 17px;
  font-weight: 100;
}
.button__wrapper {
  width: 100%;

  height: 6vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.email__logo {
  width: 15px;
  margin: 0px 6px;
  margin-top: 4px;
}
.email__button {
  background-color: aliceblue;
  width: 85%;
  height: 4vh;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 300ms ease;
}
.email__button:hover {
  scale: 1.1;
  transition: all 300ms ease;
}
.email__button:active {
  scale: 1;
  transition: all 300ms ease;
}

/* INTEREST/ABOUT */

.about__interest {
  width: 85%;
  font-size: 15px;
  line-height: 0.95em;
}

/* SOCIALS */

.socials__logo {
  width: 100%;
  background-color: rgb(14, 14, 19);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.Logo_social {
  width: 22px;
  filter: invert(100%);
  padding: 8px 0px;
  transition: all 300ms ease;
}
.Logo_social:hover {
  scale: 1.1;
  transition: all 300ms ease;
}
.Logo_social:active {
  scale: 1;
  transition: all 300ms ease;
}

/* SMALL PHONES, TABLETS , LARGE SMART PHONES*/
@media (max-width: 1024px) {
  #root{
    margin: 10px 0px;
    height: auto;
  }
}
@media (max-width: 768px) {
  body{
    height: auto;
  }
  #root{
    max-width:290px ;
    width: 100%;
    margin: 10px 0px;
  }
  .Business__card {

    height: auto;
  }
  .profilePic{
    width: 290px;
  }
}

